
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import info from "./warningcont.vue";
export default {
components: {
Scroller,
Spinner,
info
},
data() {
return {
listData: [],
warningArr: [],
pullupEnabled: true,
heightObj: {
height: "calc(100% - .9rem)"
},
title: {
name: "safeguarding",
flag: false,
backUrl: "/focus?jump=false",
hiddenGoBack:true
},
status: {
pullupStatus: "default",
pulldownStatus: "default"
},
pullupConfig: {
content: "",
downContent: "",
upContent: "",
loadingContent: ""
},
defaultImg:
"this.src='" + require("../../assets/images/1108101904.png") + "'",
warnid: "",
isAndroid: false,
currpage: 1,
showDetail: false,
infoId: "",
isMove: true
};
},
computed: {
classOption() {
return {
step: 0.5,
limitMoveNum: 1, //这个是修改moveSwitch()之前的使用方法，这里的数值指的是数据条数
openTouch: false

};
}
},
created() {
this.refresh();
this.isAndroid = this.$isAndroid();
/* if(!this.isAndroid){
this.title.backUrl='/index';
} */
},
mounted() {
window.setI18nValue = this.$setI18nValue; 
},
methods: {
seamlessClick() {
if (this.isMove) {
this.$refs.seamless.enter();
} else {
this.$refs.seamless.leave();
}
this.isMove = !this.isMove;
},
closeInfo() {
this.showDetail = false;
},
errorChange(_this) {
console.log(_this);
},
loadMore() {
var _this = this;
setTimeout(() => {

_this.currpage++;
_this.refresh();
setTimeout(() => {
this.$refs.scroller.donePullup();
}, 10);
}, 10);
},
refreshload() {
this.refresh();
},
refresh() {

var qurl =
this.$myConfig.ip + ":" + this.$myConfig.port + "phone/getMessagesWX";
var _this = this;
_this.$vux.loading.show({ text: _this.$t("loading") });
var i18nstr = _this.$i18n.locale;
if (i18nstr != "") {
i18nstr = i18nstr.split("-")[0];
}
let a_data= {
key: this.$myConfig.key,
messageType: "ALL",
language: i18nstr,
pageindex: _this.currpage,
pagesize: "10"
};
let method='POST';
let testUrl=window.g.test_getMessagesWX;
if(testUrl) {
qurl=testUrl;
method='GET';
}
$.ajax({
type: method,
url: qurl,
data: a_data, //All 24小是发布过的||NOW 24小时正在发布的


dataType: "json",
async: true,
success: function(data) {

_this.$vux.loading.hide();
_this.$refs.scroller.donePulldown();
if (data.message == "") {
_this.showBottomToast(_this.$t("nohasdata"));
_this.$vux.loading.hide();
return false;
}
var warninglist = data.message;
if (warninglist.length > 0) {
if(typeof(warninglist)==='string') {
warninglist=JSON.parse(data.message);
}else {
for (var i = 0; i < warninglist.length; i++) {
warninglist[i] = JSON.parse(warninglist[i]);
}
}

_this.warningArr = warninglist;






_this.warningArr.sort((x,y)=>x.warntime>y.warntime?-1:1);
} else {
if (qtype == undefined) {
_this.warningArr = [];
}
_this.showBottomToast(_this.$t("nohasdata"));
}
_this.$nextTick(() => {
setTimeout(() => {

}, 10);
});
},
error: function(data) {
console.log("請求失敗");
_this.$vux.loading.hide();

}
});

},
changePullupStatus(enabled) {
if (enabled) {
this.$refs.scroller.enablePullup();
this.pullupEnabled = true;
} else {
this.$refs.scroller.disablePullup();
this.pullupEnabled = false;
}
},
showBottomToast(val) {

this.$showBottomToast(val, "bottom");
},
jumpPhoneDial() {
try {
this.$jumpQuery();
} catch (e) {
console.log(e);
this.$router.push("/query/query");
}
},
getDateString(dt) {
let r='';
try {
let d= new Date(Date.parse(dt));
r= `${d.getFullYear()}/${d.getMonth()+1}/${d.getDate()} ${d.getHours()}:${d.getMinutes()}`;
}catch (e){
console.log(dt,e);
}
return r;
},
getDay(dt) {
let r='';
try {
let d= new Date(Date.parse(dt));
r= r= `${d.getMonth()+1}月${d.getDate()}日`;
}catch (e){
console.log(dt,e);
}
return r;
}
}
};



